.addRemoveIcon {
  height: 25px;
  line-height: 25px;
  width: 25px;
  text-align: center;
  font-size: 25px;
  cursor: pointer;
  border: 1px solid #fcd200;
  border-radius: 50%;
  background-color: #ffd814;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

.addRemoveIcon:hover {
  background: #ddb70dbe;
  border-color: #f2c200;
  -webkit-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

@-webkit-keyframes bump {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  10% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  30% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  50% {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bump {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  10% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  30% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  50% {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

